import {
  Component,
  OnDestroy,
  inject,
  output
} from '@angular/core';
import { DeleteConfirmationDialogComponent } from '../../components/deleteConfirmationDialog/deleteConfirmationDialog.component';
// eslint-disable-next-line @nx/enforce-module-boundaries
import {
  ClientsService,
  ProgramDataService,
  ProgramsService,
  ShareClientsDataService,
} from '@slowautomobile/exercises';
import { Location } from '@angular/common';

import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog } from '@angular/material/dialog';
import { lastValueFrom, Subscription, timer } from 'rxjs';
import { Router } from '@angular/router';
import { MatIcon } from '@angular/material/icon';
import { MatTooltip } from '@angular/material/tooltip';
import { MatIconButton } from '@angular/material/button';
import { MatDivider } from '@angular/material/divider';

@Component({
    selector: 'ui-program-options-bar',
    templateUrl: './program-options-bar.component.html',
    styleUrl: './program-options-bar.component.scss',
    imports: [MatDivider, MatIconButton, MatTooltip, MatIcon]
})
export class ProgramOptionsBarComponent implements OnDestroy {
  private programsService = inject(ProgramsService);
  private _snackBar = inject(MatSnackBar);
  dialog = inject(MatDialog);
  private router = inject(Router);
  private programDataService = inject(ProgramDataService);
  private shareClientsDataService = inject(ShareClientsDataService);
  private clientsService = inject(ClientsService);
  private location = inject(Location);
  subscriptions = new Subscription();

  programId = this.programDataService.programId;
  clientInfo = this.shareClientsDataService.clientInfo;
  // selectedTabIndex = this.shareClientsDataService.selectedTabIndex;
  readonly printProgramEvent = output<void>();

  openDeleteConfirmationDialog(itemId: string): void {
    const dialogRef = this.dialog.open(DeleteConfirmationDialogComponent, {
      width: '312px',
      data: {
        title: $localize`:@@DeleteConfirmationDialog.deleteTitleProgram:Delete selected program?`,
        message: this.clientInfo().clientName
          ? $localize`:@@DeleteConfirmationDialog.deleteTitleClientProgram:Deleting the selected program will remove it from clients profile.`
          : $localize`:@@DeleteConfirmationDialog.deleteTitleUserProgram:Deleting the selected program will remove it from your account.`,
        // message:
        //   'Deleting the selected program will remove it from your account.',
      },
      panelClass: 'custom-dialog',
    });

    const subscription = dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // User confirmed deletion, handle deletion logic here
        console.log('User confirmed deletion');
        this.deleteProgram(itemId);
        dialogRef.close();
      } else {
        // User canceled deletion
        console.log('User canceled deletion');
        dialogRef.close();
      }
    });
    this.subscriptions.add(subscription);
  }

  deleteProgram(programId: string) {
    if (this.clientInfo().clientName) {
      const subscription = this.clientsService
        .deleteClientProgram(
          this.clientInfo().clientId,
          this.clientInfo().programPosition,
        )
        .subscribe(deletedProgram => {
          console.log(deletedProgram);
          lastValueFrom(timer(2000)).then(() => {
            this.location.back();
          });
        });
      this.subscriptions.add(subscription);
    } else {
      const subscription = this.programsService
        .deleteProgram(programId)
        .subscribe(() => {
          console.log(programId);
          lastValueFrom(timer(2000)).then(() => {
            // this.selectedTabIndex.set(1);
            this.router.navigateByUrl(`programs`);
          });
        });
      this.subscriptions.add(subscription);
    }

    this._snackBar.open($localize`:@@Snackbar.programDeleted:Program deleted`, '', {
      duration: 2000,
      panelClass: ['custom-snackbar-style'],
    });
  }

  printProgram() {
    this.printProgramEvent.emit();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
