import { inject, Injectable } from '@angular/core';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { ComponentPortal } from '@angular/cdk/portal';
import { BlankOverlayComponent, SpinnerComponent } from '@slowautomobile/ui';

@Injectable({
  providedIn: 'root',
})
export class LoadingOverlayService {
  private overlay = inject(Overlay);

  private overlayRef: OverlayRef | null = null;

  private loadingCount = 0;
  private spinnerShown = false;

 show(showSpinner: boolean = true): void {
    this.loadingCount++;

    if (this.loadingCount === 1) {
      this.createOverlay(showSpinner);
      this.spinnerShown = showSpinner;
    }
  }

  hide(): void {
    this.loadingCount = Math.max(0, this.loadingCount - 1);

    if (this.loadingCount === 0 && this.overlayRef) {
      this.overlayRef.detach();
      this.overlayRef.dispose();
      this.overlayRef = null;
      this.spinnerShown = false;
    }
  }

  private createOverlay(showSpinner: boolean): void {
    const positionStrategy = this.overlay
      .position()
      .global()
      .centerHorizontally()
      .centerVertically();

    this.overlayRef = this.overlay.create({
      positionStrategy,
      hasBackdrop: true,
      backdropClass: 'cdk-overlay-transparent-backdrop',
      scrollStrategy: this.overlay.scrollStrategies.block(),
    });

    if (showSpinner) {
      const spinnerPortal = new ComponentPortal(SpinnerComponent);
      this.overlayRef.attach(spinnerPortal);
    } else {
      const blankPortal = new ComponentPortal(BlankOverlayComponent);
      this.overlayRef.attach(blankPortal);
    }
  }
}
