import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';
import { Muscle } from '../../models/muscle';

@Injectable({
  providedIn: 'root',
})
export class MusclesService {
  private http = inject(HttpClient);

  apiURLMuscles = environment.apiURL + 'muscles';

  getMuscles(): Observable<Muscle[]> {
    return this.http.get<Muscle[]>(this.apiURLMuscles);
  }

  getMuscle(muscleId: string): Observable<Muscle> {
    return this.http.get<Muscle>(`${this.apiURLMuscles}/${muscleId}`);
  }

  createMuscle(muscle: Muscle) {
    return this.http.post(this.apiURLMuscles, muscle);
  }

  updateMuscle(muscle: Muscle): Observable<Muscle> {
    return this.http.put<Muscle>(`${this.apiURLMuscles}/${muscle.id}`, muscle);
  }

  deleteMuscle(muscleId: string): Observable<Muscle> {
    return this.http.delete<Muscle>(`${this.apiURLMuscles}/${muscleId}`);
  }
}
