import {
  AfterViewInit,
  Component,
  inject,
  Input,
  ViewChild,
  output,
} from '@angular/core';
import { MatSort, Sort, MatSortModule } from '@angular/material/sort';
import { MatTableDataSource, MatTableModule } from '@angular/material/table';
import { MatPaginator, MatPaginatorModule } from '@angular/material/paginator';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { Program, ShareStateService } from '@slowautomobile/exercises';
import { ThemeManagerService } from '../../services/theme-manager.service';
import { MatIcon } from '@angular/material/icon';
import { MatTooltip } from '@angular/material/tooltip';
import { MatIconButton } from '@angular/material/button';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { FormsModule } from '@angular/forms';
import {
  MatButtonToggleGroup,
  MatButtonToggle,
} from '@angular/material/button-toggle';
import { MatInput } from '@angular/material/input';
import { MatFormField, MatLabel } from '@angular/material/form-field';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';


@Component({
  selector: 'ui-programs-table',
  templateUrl: './programs-table.component.html',
  styleUrl: './programs-table.component.scss',
  imports: [
    MatFormField,
    MatLabel,
    MatInput,
    MatButtonToggleGroup,
    FormsModule,
    MatButtonToggle,
    OverlayscrollbarsModule,
    MatTableModule,
    MatSort,
    MatSortModule,
    MatIconButton,
    MatTooltip,
    MatIcon,
    MatPaginatorModule,
    MatProgressSpinnerModule
  ],
})
export class ProgramsTableComponent implements AfterViewInit {
  private shareStateService = inject(ShareStateService);

  isFetchingData = this.shareStateService.isFetchingData;

  displayedColumns: string[] = ['name', 'numberOfExercises', 'type', 'options'];
  @Input() isAdmin = false;
  @Input() isAdminProgram = false;
  @Input() dataSource = new MatTableDataSource<Program>();
  @Input() listOption = 'all';
  @Input() mainProgramsList = false;
  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatPaginator) paginator!: MatPaginator;

  readonly getProgramsEvent = output<string>();
  readonly reuseEditProgramEvent = output<{
    programPosition: number;
    programId: string;
  }>();
  readonly openEditProgramEvent = output<string>();
  readonly openDeleteConfirmationDialogEvent = output<{
    itemId: string;
    listOption: string;
  }>();

  readonly favoriteProgram = output<Program>();
  readonly removeFavoriteProgram = output<Program>();

  themeManager = inject(ThemeManagerService);
  themeSignal = this.themeManager.themeSignal;

  // @Output() viewProgramEvent: EventEmitter<any> = new EventEmitter();
  // @Output() addProgramToClientEvent: EventEmitter<any> = new EventEmitter();

  getPrograms(listOption: string) {
    console.log(this.dataSource.data);
    this.getProgramsEvent.emit(listOption);
    console.log(this.dataSource.data);
  }

  reuseEditProgram(programPosition: number, programId: string) {
    this.reuseEditProgramEvent.emit({ programPosition, programId });
  }

  openEditProgram(programId: string) {
    this.openEditProgramEvent.emit(programId);
  }

  openDeleteConfirmationDialog(itemId: string, listOption: string) {
    this.openDeleteConfirmationDialogEvent.emit({ itemId, listOption });
  }

  // viewProgram(programId: string) {
  //   this.viewProgramEvent.emit(programId);
  //   console.log('this is open view program', programId);
  // }

  // addProgramToClient(programId: string) {
  //   this.addProgramToClientEvent.emit(programId);
  // }

  addToFavorites(program: Program) {
    program.isFavorite = true;
    this.favoriteProgram.emit(program);
    console.log(program);
  }

  removeFromFavorites(program: Program) {
    program.isFavorite = false;
    this.removeFavoriteProgram.emit(program);
    console.log(program);
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}
