import { HttpInterceptorFn } from '@angular/common/http';
import { LoadingOverlayService } from './loading-overlay.service';
import { inject } from '@angular/core';
import { finalize } from 'rxjs';
import { environment } from '@env/environment';

export const loadingInterceptor: HttpInterceptorFn = (req, next) => {

   // Default: hide spinner
  let shouldShowSpinner = false;
console.log('x-show-spinner value:', req.headers.get('x-show-spinner'));
  // Check if header exists and is explicitly set to 'true'
  if (req.headers.has('x-show-spinner')) {
    shouldShowSpinner = req.headers.get('x-show-spinner') === 'true';
  console.log('show spinner: ', shouldShowSpinner);
  }

  
  const loadingOverlayService = inject(LoadingOverlayService);
  loadingOverlayService.show(shouldShowSpinner);

  return next(req).pipe(finalize(() => loadingOverlayService.hide()));
};
