import {
  ChangeDetectionStrategy,
  Component,
  input,
  OnInit,
  output,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatListModule } from '@angular/material/list';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatButtonModule } from '@angular/material/button';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { Program } from '@slowautomobile/exercises';

@Component({
  selector: 'ui-favorite-programs',
  imports: [
    CommonModule,
    MatIconModule,
    MatListModule,
    MatButtonModule,
    MatTooltipModule,
    OverlayscrollbarsModule,
  ],
  templateUrl: './favorite-programs.component.html',
  styleUrl: './favorite-programs.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FavoriteProgramsComponent implements OnInit {
  readonly isAdmin = input<boolean>();
  readonly themeSignal = input<any>();
  readonly favoritePrograms = input<any[]>();

  readonly reuseEditProgramEvent = output<Program>();
  readonly openEditProgramEvent = output<Program>();

  ngOnInit(): void {}

  reuseEditProgram(program: Program) {
    this.reuseEditProgramEvent.emit(program);
  }

  openEditProgram(program: Program) {
    this.openEditProgramEvent.emit(program);
  }
}
