import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  input,
  Input,
  OnDestroy,
  signal,
  ViewChild,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { toSignal } from '@angular/core/rxjs-interop';
import { interval, map, takeUntil, takeWhile, timer } from 'rxjs';

@Component({
  selector: 'ui-video-player',
  imports: [CommonModule, MatProgressBarModule],
  templateUrl: './video-player.component.html',
  styleUrl: './video-player.component.scss',
})
export class VideoPlayerComponent implements AfterViewInit {
  @ViewChild('videoPlayer', { static: false })
  videoPlayer!: ElementRef<HTMLVideoElement>;
  @Input() videoSrc!: string | undefined;
  @Input() thumbnail?: string | undefined;
  // @Input() isModal()? = true;
  isModal = input<boolean>(true);
  isDash = input<boolean>(false);
  private video!: HTMLVideoElement;
  isPlaying = signal<boolean>(false);
  private hoverTimeout: any;

  progressValue = signal(0);
  private progressInterval: any;
  mouseOver = signal(false);

  ngAfterViewInit() {
    this.video = this.videoPlayer.nativeElement;
  }

  logVideoTime() {
    if (!this.isPlaying()) {
      return;
    }

    const currentTime = this.videoPlayer.nativeElement.currentTime;
    const duration = this.videoPlayer.nativeElement.duration;
    const percent = (currentTime / duration) * 100;
    this.progressValue.update(() => +percent.toFixed(1));
  }

  public getIsModalIsPlayingStatus() {
    return !this.isModal() && !this.isPlaying();
  }

  onMouseEnter() {
    if (this.isModal()) return;
    this.mouseOver.set(true);
    // this.progressValue.set(0);

    const interval = setInterval(() => {
      this.progressValue.update(value => value + 50);
      if (this.progressValue() >= 100) {
        this.progressValue.set(100);

        clearInterval(interval);
      }
    }, 100);

    // Store interval reference to clear on mouse leave
    this.progressInterval = interval;

    this.hoverTimeout = setTimeout(() => {
      this.playVideo();
    }, 500);
  }

  onMouseLeave() {
    if (this.isModal()) {
      return;
    }
    this.progressValue.set(0);
    this.pauseVideo();
    clearTimeout(this.hoverTimeout);
    clearInterval(this.progressInterval);
  }

  // Play video on mouse enter
  async playVideo() {
    // if (this.isPlaying) return; // Prevents unnecessary restarts
    if (!this.isModal() && !this.isPlaying()) {
      try {
        // Load the video explicitly
        this.video.load();

        // Wait for the video to be ready
        await new Promise<void>(resolve => {
          this.video.addEventListener('canplay', () => resolve(), {
            once: true,
          });
        });

        // Attempt to play the video
        this.isPlaying.set(true);
        await this.video.play();
        this.video.loop = true;
        // console.log('Video is playing');
      } catch (error) {
        console.error('Video playback failed:', error);
      }
    }
  }

  // Pause video on mouse leave
  pauseVideo() {
    this.isPlaying.set(false);
    this.video.loop = false;
    this.video.pause();
    // console.log('Video is paused');
  }

  togglePlay() {
    const video = this.videoPlayer.nativeElement;
    if (video.paused) {
      video.play();
    } else {
      video.pause();
    }
  }
}
