import {
  AfterViewInit,
  Component,
  inject,
  Input,
  ViewChild,
  output,
  input,
} from '@angular/core';
import {
  MatPaginator,
  MatPaginatorIntl,
  MatPaginatorModule,
} from '@angular/material/paginator';
import { MatSort, MatSortHeader } from '@angular/material/sort';
import {
  MatTableDataSource,
  MatTable,
  MatColumnDef,
  MatHeaderCellDef,
  MatHeaderCell,
  MatCellDef,
  MatCell,
  MatHeaderRowDef,
  MatHeaderRow,
  MatRowDef,
  MatRow,
  MatNoDataRow,
} from '@angular/material/table';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { Program, ShareStateService } from '@slowautomobile/exercises';
import { ThemeManagerService } from '../../services/theme-manager.service';
import { MatIcon } from '@angular/material/icon';
import { MatTooltip } from '@angular/material/tooltip';
import { MatIconButton } from '@angular/material/button';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { MatInput } from '@angular/material/input';
import { MatFormField, MatLabel } from '@angular/material/form-field';
import { MatDivider } from '@angular/material/divider';
import { PaginatorI18nService } from '../../services/paginator-i18n.service';

@Component({
  selector: 'ui-basic-programs-table',
  templateUrl: './basic-programs-table.component.html',
  styleUrl: './basic-programs-table.component.scss',
  imports: [
    MatDivider,
    MatFormField,
    MatLabel,
    MatInput,
    OverlayscrollbarsModule,
    MatTable,
    MatSort,
    MatColumnDef,
    MatHeaderCellDef,
    MatHeaderCell,
    MatSortHeader,
    MatCellDef,
    MatCell,
    MatIconButton,
    MatTooltip,
    MatIcon,
    MatHeaderRowDef,
    MatHeaderRow,
    MatRowDef,
    MatRow,
    MatNoDataRow,
    MatPaginatorModule,
  ],
  providers: [{ provide: MatPaginatorIntl, useClass: PaginatorI18nService }],
})
export class BasicProgramsTableComponent implements AfterViewInit {
  private shareStateService = inject(ShareStateService);

  // isFetchingData = this.shareStateService.isFetchingData;

  displayedColumns: string[] = ['name', 'options'];
  @Input() dataSource = new MatTableDataSource<Program>();
  isClient = input<boolean>();
  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatPaginator) paginator!: MatPaginator;

  readonly getProgramsEvent = output<string>();

  readonly viewProgramEvent = output<string>();
  readonly addProgramToClientEvent = output<string>();

  themeManager = inject(ThemeManagerService);
  themeSignal = this.themeManager.themeSignal;

  getPrograms(listOption: string) {
    console.log(this.dataSource.data);
    this.getProgramsEvent.emit(listOption);
    console.log(this.dataSource.data);
  }

  viewProgramModal(programId: string) {
    this.viewProgramEvent.emit(programId);
    // console.log('this is open view program', programId);
  }

  addProgramToClient(programId: string) {
    this.addProgramToClientEvent.emit(programId);
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}
