import {
  ChangeDetectionStrategy,
  Component,
  inject,
  input,
  OnInit,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { VideoPlayerComponent } from '../video-player/video-player.component';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { MatRippleModule } from '@angular/material/core';
import { LocalstorageService } from '@slowautomobile/users';
import { Router } from '@angular/router';

@Component({
  selector: 'ui-favorite-exercises',
  imports: [
    CommonModule,
    VideoPlayerComponent,
    OverlayscrollbarsModule,
    MatRippleModule,
  ],
  templateUrl: './favorite-exercises.component.html',
  styleUrl: './favorite-exercises.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FavoriteExercisesComponent implements OnInit {
  private router = inject(Router);
  private localstorageService = inject(LocalstorageService);

  readonly favoriteExercises = input<any[]>();
  readonly themeSignal = input<any>();

  selectedLanguage!: 'en' | 'de' | 'sr';

  private _getUserLanguageToken(): 'en' | 'de' | 'sr' {
    return <'en' | 'de' | 'sr'>(
      this.localstorageService.getToken('userLanguage')?.split('-')[0]
    );
  }

  ngOnInit(): void {
    this.selectedLanguage = this._getUserLanguageToken();
  }

  viewExercise(exerciseId: string) {
    this.router.navigate(['/exercise', exerciseId]);
  }
}
